.feed-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative; // Add relative positioning for absolute child
}

.virtual-scroll-viewport {
  height: calc(100vh - 250px);
  width: 100%;
}

// Center the initial loader in the viewport
.loading-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
}

// Style for loading more at bottom
.loading-more {
  padding: 1rem;
  text-align: center;
}

.date-group {
  position: relative;
}

.feed-item {
  transition: background-color 0.3s;

  &.new-item {
    animation: highlightNew 3s ease-out;
  }
}

@keyframes highlightNew {
  0% {
    background-color: var(--highlight-bg);
  }
  100% {
    background-color: transparent;
  }
}

// Add responsive styles for small screens
@media (max-width: 768px) {
  .virtual-scroll-viewport {
    height: calc(100vh - 180px);
  }
}
